.pizzaPageWrapper {
    text-align: center;
 }
.pizzaCard{
    position: relative;
    
    
}
.imgPricesSizesWrapper {
    background: gray;
    width: 100%;
    border-radius: 30px;
    display: flex;
    filter: grayscale(60%);
   
    &:hover{
        transition: all ease-in-out 0.3s;
        filter: grayscale(0%);
    }
    img {
        width: 340px;
        height: 266px;
        border-radius: 30px;
    }
}
.sizeAndPriceWrapper {
    display: block;
    .sizes {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
    .prices {
        display: flex;
    justify-content: space-evenly;
    margin-top: 10px;
    .price1 {
        display: flex;
    }
    .price2 {
      display: flex;
    }
}
li {
    list-style-type: none;
}
}
.sizePriceButtonWrapper {
  width: 250px;
  display: block;
  justify-content: space-around;
  align-items: center;
  .sizeAndPriceWrapper{
      position: absolute;
      top: 20%;
      left: 68%;

  }
  .btn {
      text-align: center;
      position: absolute;
      top: 91.7%;
    left: 88.9%;
    
}
}


@media screen and (max-width: 1244px) {
    .imgPricesSizesWrapper{
    flex-direction: column;
    justify-content: center;
    background: none;
}
.sizePriceButtonWrapper{
    display: flex;
    .sizeAndPriceWrapper{
        position: absolute;
        top: 100%;
        left: 10%;
    
    }
    .btn{
        top: 104%;
        left: 70%;
    }
}
   .pizzaCard{
    margin-top: 50px;
    padding-bottom: 50px;
    background: gray;
    border-radius: 20px;
   }

  }
